body {
  margin: 0;
  padding: 40px 80px 100px 80px;
  font-family: SF Pro Text, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e5e5e5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sender {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 800px;
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding-bottom: 30px;
  padding-left: 30px;
  padding-right: 30px;
  font-family: SF Pro Text;
}

.sender_alertRight {
  color: #ff6666;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  margin-top: auto;
  margin-bottom: -20px;
  margin-right: 25%;
}

.sender_alertLeft {
  color: #ff6666;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-top: auto;
  margin-bottom: -20px;
  margin-left: 1.5%;
}

.sender_form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.sender_h1 {
  font-size: 30px;
  line-height: 40px;
}

.sender_input {
  height: 40px;
  margin-right: -1px;
  padding-left: 10px;
  width: 49%;
  box-sizing: border-box;
  font-family: SF Pro Text;
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  opacity: 0.4;
}
.sender_theme {
  width: 98.5%;
}

.sender_msg {
  width: 98.5%;
  height: 140px;
  resize: none;
}

.sender_label:not(:nth-child(4n)) {
  margin-bottom: 25px;
  font-size: 16px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.6);
}

.sendBtn {
  margin-top: 10px;

  width: 140px;
  height: 40px;
  background: linear-gradient(180deg, #45a6ff 0%, #0055fb 100%), #c4c4c4;
  border-radius: 5px;
  font-family: SF Pro Text;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  border: none;
  color: #ffffff;
}

.sendBtn:focus {
  border: 2px solid #45a5ff;
  border-radius: 7px;
  outline: none;
  background: linear-gradient(180deg, #45a6ff 0%, #0055fb 100%), #c4c4c4;
}

.sendBtn:hover {
  background: linear-gradient(
      0deg,
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0.15)
    ),
    linear-gradient(180deg, #45a6ff 0%, #0055fb 100%), #c4c4c4;
}

.sendBtn:active {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.15)),
    linear-gradient(180deg, #45a6ff 0%, #0055fb 100%), #c4c4c4;
}

.sendBtn:disabled {
  background: linear-gradient(0deg, #c4c4c4, #c4c4c4),
    linear-gradient(180deg, #45a6ff 0%, #0055fb 100%);
}

.attachBtn {
  width: 171px;
  height: 26px;
  background-color: inherit;
  border: none;
  cursor: pointer;
  font-size: 16px;
  line-height: 20px;
  color: #0055fb;
}

.attachBtn_text {
  margin-left: 5px;
}

.attachBtn:focus {
  border: 2px solid #45a5ff;
  border-radius: 7px;
  outline: none;
}

.attachBtn:hover {
  color: rgb(41, 112, 255);
}

.dropArea_wrapper {
  width: 790px;
  padding-left: 0;
  padding-right: 0;
  margin-top: 4px;
}

.dropArea {
  position: absolute;
  left: 109px;
  top: 123px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 800px;
  background: rgba(255, 255, 255, 0.808);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.253);
  border-radius: 5px;
  outline: lightgrey dashed 2px;
  outline-offset: -10px;
}

.dropArea_h1 {
  font-size: 30px;
  text-align: center;
  margin-bottom: inherit;
}

.dropArea_p {
  line-height: 25px;
  text-align: center;
}

.sender_delBtn {
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  color: #FF6666;
  border: none;
  background-color: white;
  border: none;
  cursor: pointer;
  background-color: white;
  font-size: 16px;
}
.fileList {
  width: 790px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 0px;
  padding-right: 0px;
}

.fileList_item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
  width: 390px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 9px;
  border: 1px solid #dddddd;
  box-sizing: border-box;
  border-radius: 5px;
}

.fileList_item svg {
  margin-right: 10px;
}

.logo {
  margin-bottom: 20px;
}
div {
  width: 800px;
  padding-left: 30px;
  padding-right: 30px;
}

table {
  width: inherit;
  border-collapse: collapse;
  line-height: 25px;
}

.table_head {
  border-bottom: 1px solid rgb(65, 63, 63);
  color: rgb(160, 158, 158);
}

th:nth-child(1) {
  width: 100px;
}

th:nth-child(2) {
  width: 490px;
}

th:not(:nth-child(3)) {
  text-align: left;
  font-family: SF Pro Text;
  line-height: 25px;
}

th:nth-child(3) {
  text-align: end;
  font-family: SF Pro Text;
  line-height: 25px;
}

td:nth-child(3) {
  text-align: end;
}

.table_status-sended {
  color: #03a100;
}
.table_status-queue {
  color: rgb(160, 158, 158);
}
.table_status-error {
  color: #ff6666;
}

tr {
  height: 30px;
}

.msgInQueue {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 800px;
  height: 320px;
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.msgInQueue_h1 {
  font-size: 30px;
